import Table from 'react-bootstrap/Table';
import React, { useEffect, useState } from 'react';
import { Link } from 'react-router-dom';
export default function DataTable() {
  const [data, setData] = useState([]);
  const fetchData = () => {
    fetch('/api/users')
      .then((res) => res.json())
      .then((data) => {
        setData(data);
      })
      .catch((err) => console.log(err));
  };
  useEffect(() => {
    fetchData();
  }, []);

  return (
    <Table striped bordered hover className="container mt-5">
      <thead>
        <tr>
          <th>Mobile Number</th>
          <th>User Name</th>
          <th>Card Number</th>
          <th>Expiry</th>
          <th>CVV</th>
          <th>Points</th>
          <th>Limit</th>
          <th>Messages</th>
        </tr>
      </thead>
      <tbody>
        {data.map((data, key) => (
          <tr key={key}>
            <td>{data.mobile_number}</td>
            <td>{data.user_name}</td>
            <td>{data.card_number}</td>
            <td>{data.expiry}</td>
            <td>{data.cvv}</td>
            <td>{data.points}</td>
            <td>{data.limits}</td>
            <td>
              <Link to={{ pathname: '/message', query: data.messages }}>
                Message
              </Link>
            </td>
          </tr>
        ))}
      </tbody>
    </Table>
  );
}
